<template>
  <div class="register">
    <div class="register-content">
      <div style="padding-top: 2rem;color: #303133;letter-spacing: 4px;">
        <h1><strong style="">欢迎注册！</strong></h1>
      </div>
      <el-divider></el-divider>
      <div style="overflow-y: auto;height: 30rem;width:100%;text-align: left;">
        <el-form :model="form" :rules="rules" ref='form' status-icon>
          <el-form-item label="昵称" prop="nickname" label-width="80px">
            <el-input v-model="form.nickname" prefix-icon='el-icon-user' placeholder='请输入昵称' maxlength="12"></el-input>
          </el-form-item>
          <el-form-item label="个性签名" prop="signature" label-width="80px">
            <el-input v-model="form.signature" type="textarea"  placeholder="~(0_0)~" maxlength="60"
              show-word-limit show-password    ></el-input>
          </el-form-item>
          <el-form-item label="性别" prop="sex" label-width="80px">
            <template>
              <el-radio-group v-model="form.sex">
                <el-radio  :label="0">男</el-radio>
                <el-radio  :label="1">女</el-radio>
              </el-radio-group>
            </template>
          </el-form-item>
          <el-form-item label="密码" prop="password" label-width="80px">
            <el-input v-model="form.password" type="password" prefix-icon='el-icon-key' placeholder='请输入密码' maxlength="16"
              show-password></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkpassword" label-width="80px">
            <el-input v-model="form.checkpassword" type="password" prefix-icon='el-icon-key' placeholder='请输入密码' maxlength="16"
              show-password></el-input>
          </el-form-item>
          <el-form-item label="出生日期" prop="birthday" label-width="80px">
           <el-date-picker v-model="form.birthday" type="date" format="yyyy-MM-dd" value-format='yyyy-MM-dd' prefix-icon="el-icon-date" placeholder="选择生日">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="星座" prop="constellation" label-width="80px">
               <el-select v-model="form.constellation" placeholder="请选择">
                  <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
          </el-form-item>
          <el-form-item label="电话" prop="tel" label-width="80px">
            <el-input v-model="form.tel" prefix-icon='el-icon-phone' placeholder='请输入密码' maxlength="16"
             ></el-input>
          </el-form-item>
          <el-form-item label="邮箱" prop="email" label-width="80px">
            <el-input v-model="form.email"  prefix-icon='el-icon-message' placeholder='请输入密码'
              ></el-input>
          </el-form-item>
          <el-form-item label-width="80px" >
            <el-button type="primary" @click='postForm'>注册</el-button>
          </el-form-item>
          <el-link type="primary" href='#/login'>前往登录</el-link>
        </el-form>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name:'register',
    data(){
      let isNull=(rule,value,callback)=>{
        if(!value){
          callback(new Error('输入不能为空'))
        }else{
          callback()
        }
      }

      let checkPwd=(rule,value,callback)=>{
        if(value!=this.form.password){
          callback(new Error('两次密码不一致'))
        }else{
          callback()
        }
      }

      let isPhone=(rule,value,callback)=>{
        if(!(/^1[3456789]\d{9}$/.test(value))){
            callback(new Error('电话号码格式不正确'))
         } else{
           callback()
         }
       }

       let isMail=(rule,value,callback)=>{
         if(!(/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/.test(value))){
             callback(new Error('电话号码格式不正确'))
          } else{
            callback()
          }
        }

      return{
        form:{
          nickname:'',
          signature:'',
          tel:'',
          email:'',
          password:'',
          constellation:'白羊座',
          birthday:'',
          sex:0,
          checkpassword:'',
        },

        rules:{
          nickname:[
            {validator:isNull,trigger:'blur'}
          ],
          signature:[
            {validator:isNull,trigger:'blur'}
          ],
          tel:[
            {validator:isNull,trigger:'blur'},
            {validator:isPhone,trigger:'blur'}
          ],
          email:[
            {validator:isNull,trigger:'blur'},
            {validator:isMail,trigger:'blur'}
          ],
          password:[
            {validator:isNull,trigger:'blur'},
            {min:6,message:'最少输入6位',trigger:'blur'}
          ],
          constellation:[

          ],
          birthday:[
            {validator:isNull,trigger:'blur'}
          ],
          sex:[],
          checkpassword:[
            {validator:isNull,trigger:'blur'},
            {validator:checkPwd,trigger:'blur'}
          ]
        },
        options:[
          {value:'白羊座',label:'白羊座'},{value:'金牛座',label:'金牛座'},
          {value:'双子座',label:'双子座'} ,{value:'巨蟹座',label:'巨蟹座'},
          {value:'狮子座',label:'狮子座'},{value:'处女座',label:'处女座'},
          {value:'天秤座',label:'天秤座'},{value:'天蝎座',label:'天蝎座'},
          {value:'射手座',label:'射手座'},{value:'摩羯座',label:'摩羯座'}
          ,{value:'水瓶座',label:'水瓶座'},{value:'双鱼座',label:'双鱼座'}
          ]
      }
    },
    methods:{
      postForm(){
        let form = this.$refs['form']
        form.validate(pass=>{
          if(pass){
            this.axios({
              method:'post',
              url:'/user/register',
              headers:{
                'Content-Type':'application/json'
              },
              data:JSON.stringify({
                  nickname:form.model.nickname,
                  signature:form.model.signature,
                  tel:form.model.tel,
                  email:form.model.email,
                  password:form.model.password,
                  constellation:form.model.constellation,
                  birthday:form.model.birthday,
                  sex:form.model.sex,
              })
            }).then(resp=>{
              if(resp.data.status==200){
                this.$message({
                  type:'success',
                  message:'注册成功'
                })
                this.$router.push({
                  name:'accountnum',
                  params:{
                    account:resp.data.data.account
                  }
                })
              }
              if(resp.data.satus==400){
                this.$message.error("服务器正在维修~0.0~）")
              }
            }).catch(resp=>{
              this.$message.error("哎呀！服务器报错了0.0")
            })
          }
        })
      }

    }
  }
</script>

<style>
  .register{
    width: 100%;
    height: 100%;
  }
  .register-content{
    width: 300px;
    text-align: center;
    margin: 0 auto;
  }
  /* .register-content>div:last-child{
    width: 100%;

  } */
</style>
