<template>
  <div class="login">
    <div class="login-content">
      <el-form :model="form" :rules="rules" ref='form' status-icon>
        <el-form-item label="账号" :error='errormsg' prop="name" label-width="40px">
          <el-input v-model="form.name" prefix-icon='el-icon-user' placeholder='请输入账号' maxlength="16"></el-input>
        </el-form-item>
        <el-form-item label="密码" :error='errormsg' prop="password" label-width="40px">
          <el-input v-model="form.password" type="password" prefix-icon='el-icon-key' placeholder='请输入密码' maxlength="16"
            show-password></el-input>
        </el-form-item>
        <el-form-item label-width="40px">
          <el-button type="primary" @click="clickCheck()" >登录</el-button>
          <el-button @click="gotoUrl('/register')">注册</el-button>
        </el-form-item>
      </el-form>
      <div style="position: absolute;width: 230px;height: 330px;top:0;left: 20%;" v-show="visible">
        <el-popover v-model="visible" trigger="manual" width="300px" placement="top-end" popper-class="mypopper">
          <div class="yanzhengma">
            <div class="yzm-img">
              <canvas height="200px" width="200px" class="yzm-img-sb" ref="sb"></canvas>
              <canvas height="200px" width="200px" ref="cv" class="yzm-img-cv"></canvas>
            </div>
            <div class="yzm-slide">
              <div class="yzm-slide-road">
                {{tips}}
              </div>
              <div class="yzm-btn" @touchstart.prevent="drop">

              </div>
            </div>
            <div class="yzm-reforclo">
              <span class="el-icon-refresh" title="刷新验证码" @click="placeInit"></span>
              <span class="el-icon-close" title="关闭验证" @click="visible=false"></span>
            </div>
          </div>
        </el-popover>
      </div>
    </div>
  </div>
</template>

<script>
  import img from '../assets/yzm.jpg'
  export default {
    name: "login",
    data() {
      let isNull = (rule, value, callback) => {
        if (!value) {
          return callback(new Error("输入不能为空"))
        } else {
          callback()
        }
      }
      let isNum = (rule, value, callback) => {
        if (isNaN(value)) {
          return callback(new Error('输入只能为数字'))
        } else {
          callback()
        }
      }

      return {
        visible: false,
        src: img,
        tips: '点击滑块完成验证',
        place: null,
        check: false,
        looding:false,
        form: {
          name: '',
          password: ''
        },
        errormsg:'',
        rules: {
          name: [{
              validator: isNull,
              trigger: 'blur'
            },
            {
              min: 6,
              message: '输入至少6位数账号',
              trigger: 'blur'
            },
            {
              validator: isNum,
              trigger: 'blur'
            }
          ],
          password: [{
              validator: isNull,
              trigger: 'blur'
            },
            {
              min: 6,
              message: '输入至少6位数密码',
              trigger: 'blur'
            }
          ]
        }
      }
    },

    methods: {
      clickCheck() {

        this.visible = true

      },
      postForm(){
        let form = this.$refs['form']
        form.validate(pass => {
          if (pass ) {
            this.axios({
              url: '/user/login',
              method: 'post',
              params: {
                account: form.model.name,
                password: form.model.password
              }
            }).then(resp => {

                if(resp.data.status==200){
                  this.gotoUrl('/')
                }
                if(resp.data.status==400){
                  this.check=false
                  this.errormsg="账号密码错误"
                }

            }).catch(resp => {
              this.$message.error("哎呀！服务器报错了0.0")
              this.check=false

            })
          }else{
            this.check=false
          }
        })
      },
      gotoUrl(url) {
        this.$router.replace({
          path: url
        })
      },

      placeInit() {
        const random = (min, max) => {
          return Math.floor(Math.random() * (max - min + 1) + min);
        }
        //定义插入图片的参数 x坐标（60，120），y坐标100，挖去的圆形半径r:8,矩形宽度w:30
        let x = random(60, 120)
        this.place = {
          x: x,
          y: 100,
          w: 30,
          r: 8
        }
        this.canvasInit()
      },

      canvasInit() {
        let sb = this.$refs['sb'],
          cv = this.$refs['cv']
        let height = cv.height,width = cv.width
        let sb_ctx = sb.getContext('2d'),
          cv_ctx = cv.getContext('2d')
        cv.height = height
        cv.width = width
        sb.height = height
        sb.width = width
        let image = document.createElement('img')
        let place = this.place
        image.onload = function() {
          sb_ctx.drawImage(image, 0, 0, 200, 200)
          cv_ctx.drawImage(image, 0, 0, 200, 200)
          let sb_width = place.w + place.r * 2 - 4
          let imageData = sb_ctx.getImageData(place.x, place.y - place.r * 2 + 4, sb_width, sb_width )
          sb.width = sb_width
          sb_ctx.putImageData(imageData, 0, place.y - place.r * 2 + 4)
        }
        image.src = this.src
        this.drawBlock(cv_ctx, place, 'fill')
        this.drawBlock(sb_ctx, place, 'clip')
      },

      drawBlock(ctx, place, type) {
        ctx.beginPath()
        ctx.moveTo(place.x, place.y)
        ctx.lineTo(place.x + place.w / 2, place.y)
        ctx.arc(place.x + place.w / 2, place.y - place.r + 4, place.r, 0, 2 * Math.PI)
        ctx.lineTo(place.x + place.w / 2, place.y)
        ctx.moveTo(place.x + place.w, place.y)
        ctx.lineTo(place.x + place.w, place.y + place.w / 2)
        ctx.arc(place.x + place.w + place.r - 4, place.y + place.w / 2, place.r, 0, 2 * Math.PI)
        ctx.lineTo(place.x + place.w, place.y + place.w / 2)
        ctx.moveTo(place.x, place.y)
        ctx.lineTo(place.x + place.w, place.y)
        ctx.lineTo(place.x + place.w, place.y + place.w)
        ctx.lineTo(place.x, place.y + place.w)
        ctx.fillStyle = '#fff'
        ctx.lineWidth = 0.1
        ctx.strokeStyle = "#fff";
        ctx.stroke()
        ctx[type]()
        ctx.beginPath()
        ctx.arc(place.x, place.y + place.w / 2, place.r, 1.5 * Math.PI, 0.5 * Math.PI)
        ctx.globalCompositeOperation = "xor"
        ctx.fill()
      },

      drop(e) {
        let dom = e.targetTouches[0].target

        let sb = this.$refs['sb']
        let downPlace = {
          x: e.changedTouches[0].clientX,
          y: e.changedTouches[0].clientY
        }
        let place = this.place
        let x = 0
        const move = (event) => {
          x = event.changedTouches[0].clientX- downPlace.x
          if (x < 0 || x > 265) {
            return false
          }
          dom.style.left =x+'px'
          sb.style.left = x+'px'
        }
        const up = (event) => {
          document.removeEventListener("touchmove", move)
          document.removeEventListener("touchend", up)
          dom.style.left = "";
          let curx = event.changedTouches[0].clientX - downPlace.x
          //误差为1
          if ((place.x - 1) <= curx && curx <= (place.x + 1)) {
            this.check = true
            this.tips = '验证通过'
            setTimeout(() => {
              this.visible = false;
              this.tips = '点击滑块完成验证'
              sb.style.left = ""
            }, 500);
          } else {
            this.tips = "验证失败，请重试";
            this.check = false;
            sb.style.left = ""
            this.placeInit();
          }
        }
        document.addEventListener("touchmove", move)
        document.addEventListener("touchend", up)
      }
    },
    watch: {
      visible: function(val) {
        if (val) {
          this.placeInit()
          this.check = false
        }
      },
      check:function(val){
        if(val){
          this.postForm()
        }
      }
    }

  }
</script>

<style>
  .login {
    width: 100%;
    height: 100%;
    position: relative;

  }


  @media only screen and (min-height:400px) {
    .login-content {
      width: 100%;
      padding: 1.25rem;
      box-sizing: border-box;
      top: 60px;
      position: absolute;
    }

  }

  @media only screen and (min-height:550px) {
    .login-content {
      width: 100%;
      padding: 1.25rem;
      box-sizing: border-box;
      position: absolute;
      top: 10rem;
    }
  }

  @media only screen and (min-height:750px) {
    .login-content {
      width: 100%;
      height: 30%;
      padding: 1.25rem;
      box-sizing: border-box;
      position: absolute;
      top: 13.75rem;
    }
  }

  .yanzhengma {
    height: 300px;
  }

  .yzm-img {
    position: relative;
    height: 200px;
    width: 100%;
  }

  .yzm-img-sb {
    position: absolute;
    z-index: 100;
  }

  .yzm-img-cv {
    position: relative;

  }

  .mypopper {
    margin: 0 auto;
  }

  .yzm-slide {
    height: 69px;
    border-bottom: 0.1px solid #DCDFE6;
    position: relative;
  }

  .yzm-reforclo {
    width: 100%;
    height: 30px;
    font-size: 30px;
    line-height: 30px;
    text-align: right;
    letter-spacing: 7px;
  }

  .yzm-slide-road {
    background-color: #E4E7ED;
    height: 50%;
    position: relative;
    top: 25%;
    border-radius: 50px;
    line-height: 35px;
    text-align: center;
  }

  .yzm-btn {
    width: 40px;
    height: 40px;
    position: absolute;
    left: 0;
    top: 15px;
    background-image: radial-gradient(circle, #CAA4ED, #3C3572);
    border-radius: 50%;
  }
</style>
